<template>
    <div id="classMan_page">
        <el-table
                :data="tableData"
                stripe
                border
                height="100%"
                style="width: 100%">
            <el-table-column
                    type="index"
                    width="50"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="subjectName"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="subjectType"
                    :formatter="subjectTypeFormatter"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="subjectOrd"
                    label=" ">
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "grade-add",
        data(){
            return{
                tableData:[]
            }
        },
        mounted(){
            this.querySubjectData()
        },
        methods:{
            //
            subjectTypeFormatter(val){
                let newVal='';
                switch (val.subjectType+""){
                    case  '0':
                        newVal= '';
                        break;
                    case  '1':
                        newVal= '';
                        break;
                    case  '2':
                        newVal= '';
                        break;
                    case '3':
                        newVal='';
                        break;
                    default :
                        break;

                }
                return newVal;
            },
            //
            querySubjectData(){
                this.tableData=[];
                this.$axiosF('subjectInfo/list','get',{},res=>{
                    if(res.data.success){
                        this.tableData=res.data.data
                    }else{
                        this.$alert(res.data.message, '', {
                            confirmButtonText: ''})
                    }
                },err=>{
                    this.$alert(err.message, '', {
                        confirmButtonText: ''})
                })
            }
        }
    }
</script>

<style scoped>
    #classMan_page{
        height: 100%;
        padding: 5px;
    }
</style>
